@import url(https://fonts.googleapis.com/css?family=Open+Sans|Ubuntu|Harmattan);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.7.0/css/font-awesome.min.css);
* {
  outline: none !important;
}

body {
  margin: 0;
  padding: 0;

  /* font-family: "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; */
}

#root {
  display: flex;
  flex-direction: column;

  height: 100vh;

  overflow: hidden;
}

.sweet-loading {
  min-height: 100%;
  height: -webkit-fill-available;
  height: -moz-available;
  height: stretch;

  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.centered {
  align-self: center;
}
.header {
  padding: 0 1rem;

  height: 60px;
  min-height: 60px;
  background: #273c75;
  /* background: linear-gradient(to right, #0ed461 85%, #0ed4ba 100%);
  background: -moz-linear-gradient(to right, #0ed461 85%, #0ed4ba 100%);
  background: -webkit-linear-gradient(to right, #0ed461 85%, #0ed461 100%); */

  display: flex;
  flex-direction: row;
}

.nav-item {
  padding: 10px 20px;

  font-family: Ubuntu;
  font-size: .9rem;
  color: #f5f6fa;
  text-decoration: none;
  
  align-self: center;
}

.nav-item.active,
.nav-item.right.active {
  background: white;
  border-radius: 4px;

  font-weight: bold;
  color: #e1b12c;
}

.nav-item.right {
  margin-left: auto;
  color: #f5f6fa;
}
.App-wrapper {
  display: flex;

  flex-grow: 1;
}

.App-left-sidebar {
  height: calc(100vh - 60px);
  width: 400px;
  min-width: 400px;
  max-width: 400px;

  background: #f5f6fa;

  overflow-y: hidden;

  display: flex;
  flex-direction: column;
}

.App-right-sidebar {
  flex-grow: 1;
}

.App-search-header {
  height: 30px;

  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  font-family: "Open Sans";
}
.App-search-header span {
  font-size: 0.7rem;
  color: #44bd32;
  
}
.App-search-header button {
  padding: 5px 0;

  background: transparent;
  border: none;
  border-radius: 3px;

  font-size: 0.65rem;
  color: #999;

  cursor: pointer;
}

.App-surats {
  padding: 1rem;

  /* height: calc(100vh - 160px); */
  min-height: calc(100vh - 160px);

  flex-grow: 1;

  overflow-y: auto;
}

.Card {
  padding: 1rem;

  background: white;
  border-radius: 4px;
  box-shadow: 0 2px 7px 0 #dcdde1;

  display: flex;
  flex-direction: column;

  text-decoration: none;

  /* cursor: pointer; */
}
.Card:not(:last-child) {
  margin-bottom: 13px;
}

.Card-title {
  margin-bottom: 10px;

  font-family: Ubuntu;
  font-weight: bold;
  font-size: 1rem;
  color: #2f3640;
}
.Card-title-helper {
  font-weight: normal;
  color: #dcdde1;
}
.Card-title-right {
  font-size: 1.2rem;
  font-family: 'Harmattan';
  font-weight: normal;
  color: #44bd32;
  line-height: 1.2rem;

  float: right;
}

.Card-footer {
  font-family: Ubuntu;
  font-size: 0.8rem;
  color: #999;
  line-height: 1rem;
}

.Card.active {
  background: #273c75;
  /* background: linear-gradient(to bottom, #0097e6 75%, #192a56 100%);
  background: -moz-linear-gradient(to bottom, #0097e6 75%, #192a56 100%);
  background: -webkit-linear-gradient(to bottom, #0097e6 75%, #192a56 100%); */
}

.Card.active .Card-title {
  color: white;
}
.Card.active .Card-title-helper {
  color: #f1f1f1;
}
.Card.active .Card-title-right {
  color: #fbc531;
}

.Card.active .Card-footer {
  color: #e1b12c;
}
.Searchbox {
  height: 60px;
  min-height: 60px;
  max-height: 60px;

  padding: 0 1rem;

  background: #f5f6fa;
  border-bottom: 1px solid #dcdde1;

  display: flex;
  justify-items: center;
  align-items: center;
}

.Searchbox input {
  padding: 0 16px;

  height: 30px;
  width: 100%;

  border: none;
  border-radius: 4px 0 0 4px;
  box-shadow: inset 0 0 3px .5px #ddd;

  font-family: "Open Sans";
  font-size: .8rem;
  color: #333;
}
.Searchbox input:focus {
  outline: none;
}
.Searchbox input::-webkit-input-placeholder {
  color: #ccc;
}
.Searchbox input::-ms-input-placeholder {
  color: #ccc;
}
.Searchbox input::placeholder {
  color: #ccc;
}
.Searchbox button {
  padding: 0 20px;

  background: #dcdde1;

  height: 30px;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;

  border: none;
  border-radius: 0 4px 4px 0;

  color: #273c75;

  cursor: pointer;
}

.Content {
  overflow-y: hidden;

  display: flex;
  flex-direction: column;
}

.Content-detail {
  padding: 1.2rem 2rem 0 2rem;

  min-height: calc(100vh - 150px + .8rem);
  max-height: calc(100vh - 150px + .8rem);

  overflow-y: auto;
}

.Content-header {
  font-family: Ubuntu;
  font-weight: bold;
  font-size: 1.5rem;
  color: #2f3640;

  position: relative;

  display: inline;
}
