  content: "";
  position: absolute;

  bottom: -6px;
  left: 0;

  height: 3px;
  width: 100%;

  background: #0ed461;
  border-radius: 4px;
} */

.Content-body {
  padding: 1rem 0;
}

.ListBorder {
  list-style-type: none;
  padding: 0;
}

.ListBorder li {
  padding: 1.6rem 0 0 1rem;

  border-left: 4px solid #dedede;

  position: relative;
}
.ListBorder li:not(:last-child) {
  margin-bottom: 32px;
}

.Listitem-floating {
  position: absolute;
  top: -10px;
  left: -4px;

  font-family: "Open Sans";
  font-size: 0.65rem;
  color: white;
}
.Listitem-number {
  padding: 4px 8px;

  background: #44bd32;
  border-radius: 4px 0 0 0;
}

.Listitem-play {
  padding: 4px 8px; 

  background: #44bd32;
  border-radius: 0 4px 4px 0;
  border-left: 2px solid #369228;

  cursor: pointer;
}

.ListItem-body {
  margin-bottom: 24px;

  font-family: 'Harmattan';
  font-size: 1.5rem;
  text-align: right;
  line-height: 2rem;

  color: #2f3640;
}

.ListItem-footer {
  padding: .8rem 1rem;

  background: #f5f6fa;
  border-radius: 4px;

  font-family: "Open Sans";
  font-size: 0.9rem;
  line-height: 1.5rem;
}

.Player {
  height: 60px;
  min-height: 60px;
  max-height: 60px;

  padding: 0 1.2rem;

  background: #dcdde1;

  display: flex;
  justify-items: center;
  align-items: center;
}

.Input-circle-button {
  height: 30px;
  width: 30px;

  background: #44bd32;
  border-style: solid;
  border-radius: 50%;
  box-shadow: 0 0 5px 0 #939397;

  font-size: .7rem;
  color: white;
  vertical-align: middle;

  outline: none;

  cursor: pointer;
}

.Input-form-group {
  display: flex;

  align-items: center;

  font-family: 'Open Sans';
}

.Input-form-group *:focus {
  outline-color: transparent;
}
.Input-form-group *:active {
  outline-color: #e1b12c;
}

.Input-form-group.right {
  margin-left: auto;
}

.Input-form-group label {
  margin-right: 8px;

  font-size: .8rem;
  color: #273c75;
}

.Input-form-group select {
  padding: 4px 8px;

  height: 30px;
  min-width: 200px;

  background: white;
  border: none;
  border-radius: 4px;

  font-size: .8rem;
}

.Input-form-group.fit-content select {
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
}
